import { useMemo } from "react";
import { IMAGES_BY_CATEGORY } from "../constants/images";
import { Question } from "../types";
import { getRandomElement } from "../utils";

export const Card = (props: { question: Question }) => {
  const { content, level,  punishment } = props.question;
  // const imageName = (getRandomElement(
  //   IMAGES_BY_CATEGORY[category ? getRandomElement(category) : "FUNNY"]
  // ))

  const imageName = useMemo(
    () => getRandomElement(IMAGES_BY_CATEGORY[level ?? "NORMAL"]),
    [level]
  );
  console.log(`imageName ${imageName}`);
  const icon = require("../images/" + imageName);
  return (
    <div className="card">
      <div className="content">
        {content}
        {punishment && <div className="punishment">{punishment}</div>}
      </div>
      <footer className="card-footer">
        <img alt="icon" className="pointer-none icon" src={icon}></img>
      </footer>
    </div>
  );
};
