import React, { useEffect, useMemo, useRef, useState } from "react";
import { Question } from "../types";
import { getRandomInt, shuffle } from "../utils";
import { CardContainer } from "./CardContainer";
import { ShuffleCard } from "./ShuffleCard";

export const Game = ({
  questions,
  shuffle,
}: {
  questions: Question[];
  setQuestions: any;
  shuffle: () => void;
}) => {
  const [isReset, setReset] = useState<boolean>(false);
  const [currentIndex, setCurrentIndex] = useState(questions.length - 1);
  const currentIndexRef = useRef(currentIndex);

  const childRefs = useMemo<any[]>(
    () =>
      Array(questions.length)
        .fill(0)
        .map((i) => React.createRef()),
    [questions.length]
  );
  const updateCurrentIndex = (val: any) => {
    setCurrentIndex(val);
    currentIndexRef.current = val;
  };

  const canGoBack = currentIndex < questions.length - 1;

  const canSwipe = currentIndex >= 0;

  const swiped = (direction: string, nameToDelete: string, index: number) => {
    updateCurrentIndex(index - 1);
  };

  const outOfFrame = (name: string, idx: number) => {
    currentIndexRef.current >= idx && childRefs[idx].current.restoreCard();
  };

  const swipe = async (dir: any) => {
    if (canSwipe && currentIndex < questions.length) {
      await childRefs[currentIndex].current.swipe(dir); // Swipe the card!
    }
  };
  // increase current index and show card
  const goBack = async () => {
    if (!canGoBack) return;
    const newIndex = currentIndex + 1;
    updateCurrentIndex(newIndex);
    await childRefs[newIndex].current.restoreCard();
  };

  const reset = () => {
    setReset(true);
    updateCurrentIndex(questions.length - 1);

    for (let i = 0; i < questions.length; i++) {
      childRefs[i]?.current?.restoreCard();
    }
  };

  useEffect(() => {
    if (isReset) {
      setTimeout(() => setReset(false), 1500);
      shuffle();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isReset]);

  return (
    <>
      {isReset ? (
        <ShuffleCard />
      ) : (
        <CardContainer
          questions={questions}
          swiped={swiped}
          outOfFrame={outOfFrame}
          isReset={isReset}
          childRefs={childRefs}
        />
      )}
      <div className="buttons">
        <button
          style={{ backgroundColor: !canGoBack ? "#c3c4d3" : "" }}
          onClick={() => goBack()}
        >
          Quay li l trc
        </button>
        <button
          style={{ backgroundColor: !canSwipe ? "#c3c4d3" : "" }}
          onClick={() => swipe(getRandomInt(10) > 5 ? "right" : "left")}
        >
          L tip theo
        </button>
        <button onClick={() => reset()}>Xo li bi</button>
      </div>
    </>
  );
};
