import { useEffect, useState } from "react";
import "./App.css";
import "./animation.css";
import { Game } from "./components/Game";
import { url } from "./constants/constants";
import { Question } from "./types";
import { processDataFromGoogleSheet, shuffle } from "./utils";

const FILTERS = [
  {
    type: "NORMAL",
    label: "Dui D",
  },
  {
    type: "A BIT",
    label: "t t",
  },
  {
    type: "TOO MUCH",
    label: "Nc Lnh Lng",
  },
];

function App() {
  const [questions, setQuestions] = useState<Question[]>();
  const [filters, setFilters] = useState<string[]>([]);
  const [startGame, setStartGame] = useState<boolean>(false);

  useEffect(() => {
    const getData = async () => {
      fetch(url)
        .then((res) => res.text())
        .then((rep) => {
          //Remove additional text and extract only JSON:
          const jsonData = JSON.parse(rep.substring(47).slice(0, -2));
          const q = processDataFromGoogleSheet(jsonData);
          setQuestions(
            shuffle(
              q.filter((question) =>
                filters?.includes(question.level ?? "NORMAL")
              )
            )
          );
        });
    };
    if (startGame && filters) getData();
  }, [startGame, filters]);

  const shuffleCards = () => {
    if (questions) setQuestions(shuffle(questions));
  };

  const addFilter = (filter: string) => {
    let newArray = [...filters];

    const indexOfExist = newArray.indexOf(filter);
    if (indexOfExist >= 0) {
      newArray.splice(indexOfExist, 1);
    } else {
      newArray.push(filter);
    }
    setFilters(newArray);
  };

  return (
    <div className="App">
      {!startGame ? (
        <>
          <h2 className="helper">
            Chn ch  chi <br key="br"></br>{" "}
            <span key="span">(c th chn nhiu)</span>
          </h2>
          <div className="filters">
            {FILTERS.map((filter, index) => {
              return (
                <>
                  <div key={index} className="checkbox-wrapper">
                    <input
                      type="checkbox"
                      name={filter.type}
                      id={`filter-${index}`}
                      key={index}
                      onClick={() => addFilter(filter.type)}
                    />
                    <label htmlFor={`filter-${index}`}>{filter.label}</label>
                  </div>
                </>
              );
            })}
          </div>
          <div className="buttons">
            <button
              onClick={() => {
                setStartGame(true);
              }}
            >
              Chi thi! 
            </button>
          </div>
        </>
      ) : null}

      {startGame && questions ? (
        <Game
          key="game"
          questions={questions}
          setQuestions={setQuestions}
          shuffle={shuffleCards}
        />
      ) : null}
    </div>
  );
}

export default App;
