import { Question } from "./types";

export function shuffle<T>(array: T[]): T[] {
  const newArray: T[] = [];
  array.forEach((val) => newArray.push(Object.assign({}, val)));
  for (let i = newArray.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [newArray[i], newArray[j]] = [newArray[j], newArray[i]];
  }
  return newArray;
}

export function getRandomInt(max: number) {
  return Math.floor(Math.random() * max);
}

export function getRandomElement<T>(array: T[]): T {
  const index = getRandomInt(array.length);
  return array[index];
}

export function processDataFromGoogleSheet(json: Record<string, any>) {
  const rows = json.table.rows;
  // content,punishment,category,level
  const questions: Question[] = [];
  for (let i = 1; i < rows.length; i++) {
    const row = rows[i].c;
    if (!row[0]?.v) {
      continue
    }
    questions.push({
      content: row[0]?.v ?? "",
      punishment: row[1]?.v ?? "",
      category: JSON.parse(row[2]?.v ?? ["FUNNY"]) ?? ["FUNNY"],
      level: row[3]?.v ?? "NORMAL",
    });
  }
  return questions;
}
