import TinderCard from "react-tinder-card";
import { Question } from "../types";
import { Card } from "./Card";

type CardContainerProps = {
  questions: Question[];
  childRefs: React.Ref<any>[];
  isReset: boolean;
  swiped: (dir: string, content: string, index: number) => void;
  outOfFrame: (content: string, index: number) => void;
};

export const CardContainer = (props: CardContainerProps) => {
  const { questions, childRefs, isReset, swiped, outOfFrame } = props;
  return (
    <div className="cardContainer">
      {questions.map((question, index) => (
        <TinderCard
          ref={childRefs[index]}
          className={`swipe ${isReset ? "hidden" : "show"}`}
          key={question.content}
          onSwipe={(dir) => swiped(dir, question.content, index)}
          onCardLeftScreen={() => outOfFrame(question.content, index)}
        >
          <Card key={index} question={question}></Card>
        </TinderCard>
      ))}
    </div>
  );
};
